<template>
  <div class="map_page" id="map_page">
    <div class="COMMING" id="COMMING">
      COMMING SOON
    </div>

    <div class="dots" id="dots">
      {{dots}}
    </div>

    <Menu></Menu>
  </div>
</template>

<style scoped lang="css">
::-webkit-scrollbar {
  display: none;
}

div.COMMING{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* width: ; */
  font-size: 90rem;
  line-height: 1;
  font-family: FuturaLT-ExtraBold;
  color: #251714;
}

div.dots{
  position: absolute;
  top: calc(50% + 100rem);
  left: 50%;
  transform: translate(-50%, -50%);
  /* width: ; */
  font-size: 90rem;
  line-height: 1;
  font-family: FuturaLT-ExtraBold;
  color: #251714;
}



</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";

//export default
export default {
  name: "Map",
  data() {
    return {
      global: global,
      dots: ""
    };
  },
  components: {
    Menu,
  },
  beforeCreate() {
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {},
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    let i = 0
    setInterval(()=>{
      this.dots+="."
      i++
      if(i===7){
        i = 0
        this.dots = ""
      }
    }, 1000)
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  destroyed() {},

  methods: {
    refresh_root_size() {
      let static_piexl = 0;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
  },
};
</script>


